import { useMemo } from 'react'
import { useFormikContext } from 'formik'

const location = [
    '北北基',
    '桃竹苗',
    '中彰投',
    '雲嘉南',
    '宜花東',
    '高屏',
    '澎金馬',
]

const journal = [
  '當天來回',
  '兩天一夜',
  '三天以上',
]

export default () => {
  const { values } = useFormikContext()

  return useMemo(() => {
    const filterLocation = values.location ? values.location.map((v) => location[+v]) : []
    const filterJournal = values.journal ? values.journal.map((v) => journal[+v]) : []
    const filterData = filterLocation.concat(filterJournal)
    return { filter: filterData, ...values }
  }, [values])
}
