import React, { useState } from 'react'

import Module from './Module'

import Box from '../../../components/Box';
import Flex from '../../../components/Flex';
import Button from '../../../components/Button';

const Desktop = ({ region, useData }) => {
  const [all, openAll] = useState()
  return (
    <Flex
      position="absolute"
      top="4em"
      left="0"
      right="0"
      bg="white"
      px="2em"
      py="1em"
      height={!all && '45vh'}
      overflow="hidden"
      zIndex={98}
    >
      {region.map((r, i, { length }) => (
        <Box width={1 / length} key={i}>
          <Box
            mx="0.5rem"
            fontSize="1.25em"
            fontWeight="bold"
            borderBottom="1px solid"
            pb="0.625rem"
            lineHeight="1"
          >
            {r}
          </Box>
          {useData[r].map(({ name, src, isLock }, k) => (
            <Module name={name} src={src} isLock={isLock} key={k} />
          ))}
        </Box>
      ))}
      {!all && (
        <>
          <Box
            position="absolute"
            top="0"
            bottom="0"
            left="0"
            right="0"
            pointerEvents="none"
            background="linear-gradient(rgba(255, 255, 255, 0) 40%, rgba(255, 255, 255) 100%);"
          />
          <Box position="absolute" bottom="0.5em" left="50%" transform="translateX(-50%)">
            <Button.Transparent onClick={() => openAll(!all)}>
              看完整列表
            </Button.Transparent>
          </Box>
        </>
      )}
    </Flex>
  )
}

export default Desktop
