import React, { useState } from 'react'
import { BsFillCaretDownFill } from 'react-icons/bs';
import { useFormikContext } from 'formik'
import OutsideClickHandler from 'react-outside-click-handler';

import Box from '../../components/Box';
import Flex from '../../components/Flex';
import Dropdown from '../../components/Dropdown';
import Input from '../../components/Input';
import Button from '../../components/Button';
import { responsive } from '../../components/ThemeProvider/theme';

const options = [
  {
    label: '預設',
    value: 'all',
  },
  {
    label: '北到南',
    value: 'north:reverse',
  },
  {
    label: '南到北',
    value: 'north',
  },
  {
    label: '高到低',
    value: 'height:reverse',
  },
  {
    label: '低到高',
    value: 'height',
  }
]

export const filterContent = [
  {
    title: '所在地區',
    name: 'location',
    checkboxs: [
      {
        rightLabel: '北北基',
      },
      {
        rightLabel: '桃竹苗',
      },
      {
        rightLabel: '中彰投',
      },
      {
        rightLabel: '雲嘉南',
      },
      {
        rightLabel: '宜花東',
      },
      {
        rightLabel: '高屏　',
      },
      {
        rightLabel: '澎金馬',
      },
    ]
  },
  {
    title: '行程天數',
    name: 'journal',
    checkboxs: [
      {
        rightLabel: '當天來回',
        mr: '1.625em',
      },
      {
        rightLabel: '兩天一夜',
        mr: '1.625em',
      },
      {
        rightLabel: '三天以上',
      },
    ]
  }
]

const Filter = () => {
  const [open, setOpen] = useState()
  const { values, setFieldValue } = useFormikContext()
  return (
    <Box.Inline mx={responsive('2em', '4em')} mt="1em" position="relative" bg="bgGray" zIndex={33}>
      <OutsideClickHandler onOutsideClick={() => setOpen(false)}>
        <Dropdown.Field display="inline-block" width="6em" name="sort" placeholder="排序" options={options} />
        <Box ml="0.5em" display="inline-block" position="relative">
          <Button
            bg="white"
            hoverBg="white"
            border="1px solid"
            borderColor="gray"
            hoverBorder="gray"
            py="0.5em"
            px="0"
            width="5em"
            color={open ? 'cyan' : 'darkGray'}
            hoverColor="black"
            rightIcon={BsFillCaretDownFill}
            onClick={() => setOpen(!open)}
          >
            篩選
          </Button>
          {open && (
            <Box
              position="absolute"
              width={responsive('280%', '375px')}
              top="100%"
              left="0"
              px="1em"
              border="1px solid"
              borderColor="gray"
              borderRadius="0.25em"
              bg="white"
            >
              {filterContent.map(({ title, name, checkboxs }, k) => (
                <Box key={k} mt="0.75em">
                  <Flex justifyContent="space-between">
                    <Box>{title}</Box>
                    <Input
                      onChange={(e) => {
                        setFieldValue(name, e.target.checked ? checkboxs.map((l, i) => String(i)) : [])
                      }}
                      type="checkbox"
                      rightLabel="全選"
                      checked={values[name].length === checkboxs.length}
                    />
                  </Flex>
                  <Flex
                    mt="1em"
                    pb="0.5em"
                    flexWrap="wrap"
                    borderBottom="1px solid"
                    borderColor="lightGray"
                  >
                    {checkboxs.map(({ rightLabel, ...props }, index) => (
                      <Input.Field
                        width={responsive(1 / 2, 1 / 4)}
                        justifyContent={!Boolean(k) && responsive(index % 2 && 'flex-end', 'auto')}
                        name={name}
                        type="checkbox"
                        rightLabel={rightLabel}
                        key={index}
                        value={String(index)}
                        {...props}
                      />
                    ))}
                  </Flex>
                </Box>
              ))}
              <Button.Transparent
                disabled={!values.location.concat(values.journal).length}
                onClick={() => {
                    setFieldValue('location', [])
                    setFieldValue('journal', [])
                  }}
              >
                重新篩選
              </Button.Transparent>
            </Box>
          )}
        </Box>
      </OutsideClickHandler>
    </Box.Inline>
  )
}

export default Filter
