import React, { useEffect, useState, useContext } from 'react'
import styled from 'styled-components'
import { MdPlayArrow } from 'react-icons/md'
import random from 'lodash/random'
import { useHistory, Link } from 'react-router-dom';
import { IoIosLock } from 'react-icons/io';

import Box from '../../components/Box'
import Button from '../../components/Button'
import BackgroundImage from '../../components/BackgroundImage'
import Image from '../../components/Image'
import { responsive } from '../../components/ThemeProvider/theme';

import { front, back } from '../photo'

import rotateContext from './rotateContext'

const StyledBox = styled(Box)`
  display: inline-block;
  perspective: 1000px;
  .card {
    position: relative;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transform-style: preserve-3d;
  }
`
const bgColor = ['bgGreen', 'bgPink', 'bgOrange', 'gray', 'bgBlue']

const Card = ({ name, children, src, isLock, ...props }) => {
  const [state, setstate] = useState(isLock)
  const rotate = useContext(rotateContext)
  useEffect(() => {
    setstate(isLock || rotate)
  }, [rotate])
  // console.log(state, rotate)
  return (
    <StyledBox {...props}>
      <Box className="card" transform={state && 'rotateY(180deg)'} bg={bgColor[random(4)]} borderRadius="1rem">
        <BackgroundImage
          onClick={() =>
            setstate(!state)
          }
          pointerEvents={state && 'none'}
          position="relative"
          src={front[src]}
          zIndex={2}
          ratio={1}
          borderRadius="1rem"
        >
          {!front[src] && <Box position="absolute" top="0.5em" left="0.5em">{name}</Box>}
        </BackgroundImage>
        <Box
          position="absolute"
          top="0"
          left="0"
          width="100%"
          height="100%"
          backfaceVisibility="hidden"
          transform="rotateY(180deg)"
          pointerEvents={isLock && 'none'}
          onClick={() =>
            setstate(!state)
          }
        >
          <Box borderRadius="1em" bg="white" overflow="hidden">
            <Image src={back[src]} />
          </Box>
          {isLock ? (
            <Box
              position="absolute"
              left="0"
              right="0"
              top="0"
              bottom="0"
              bg="white"
              opacity="0.5"
              borderRadius="1em"
            >
              <Box position="absolute" left="50%" top="50%" transform="translate(-50%, -50%)">
                <IoIosLock size="2.5em" />
              </Box>
            </Box>
          ) : (
            <Box
              position="absolute"
              left="50%"
              bottom="1em"
              width={responsive('90%', '80%')}
              transform="translateX(-50%)"
              textAlign="center"
            >
              <Button.Transparent
                as={Link}
                rightIcon={MdPlayArrow}
                px={responsive('0.5em', '1em')}
                py="0.25em"
                color="white"
                hoverColor="white"
                bg="rgb(211, 211, 211, 0.5)"
                hoverBg="rgb(211, 211, 211, 0.5)"
                borderRadius="2em"
                fontWeight="bold"
                to={`/topics/${src}`}
                target="_blank"
                onClick={(e) => {
                  e.stopPropagation()
                }}
              >進入這座山
              </Button.Transparent>
            </Box>
          )}
        </Box>
      </Box>
    </StyledBox>
  )
}

export default Card;
