import React, { useState } from 'react'
import { useHistory } from 'react-router-dom';
import { MdMenu } from 'react-icons/md';
import { BsChevronDown } from 'react-icons/bs';
import Collapsible from 'react-collapsible';

import Box from '../../components/Box';
import Button from '../../components/Button';
import { responsive } from '../../components/ThemeProvider/theme';

const menu = [
  {
    label: '關於山模山樣',
    to: '/about',
  },
  {
    label: '登山主題特輯',
    Comp: Collapsible,
    children: (setOpen, history) => topics.map(({ topic, to }, i) => (
        <Box backgroundImage="linear-gradient(white, transparent)" key={i}>
          <Button.Transparent
            onClick={() => {
              setOpen(false)
              history.push(to)
            }}
            color="gray"
          >{topic}</Button.Transparent>
        </Box>
      )
    )
  },
  {
    label: '訂閱我們',
    to: '/about',
  },
]

const topics = [
  {
    topic: '裝備準備',
    to: ''
  },
  {
    topic: '登山組隊',
    to: ''
  },
  {
    topic: '山友責任',
    to: ''
  },
]


const Menu = ({ openList }) => {
  const [open, setOpen] = useState()
  const history = useHistory()
  return (
    <>
      <Box px={responsive('0.5em', '1em')}>
        <Button.Transparent color="lightGray" onClick={() => setOpen(!open)}>
          <MdMenu size="2.25em" />
        </Button.Transparent>
      </Box>
      <Box
        top="3.5em"
        height="100vh"
        left="0"
        bg="white"
        py="3em"
        px="5em"
        position="absolute"
        transform={open ? 'translateX(0%)' : 'translateX(-100%)'}
        transition="all 0.5s"
        zIndex={99}
      >
        {menu.map(({ label, to, Comp = Button.Transparent, children }, i) => (
          <Comp
            trigger={
              <Box position="relative" cursor="pointer" mb="0.875em">
                {label}
                <Box position="absolute" top="0" right="105%">
                  <BsChevronDown />
                </Box>
              </Box>
            }
            onClick={() => {
              history.push(to)
              setOpen(!open)
              openList(false)
            }}
            transitionTime={200}
            mb="0.875em"
            key={i}
          >{to ? label : children(setOpen, history)}
          </Comp>
        ))}
      </Box>
    </>
  )
}

export default Menu
